<template>
	<div class="referral-program-table-outer-container">
		<div class="rating-table">
			<div class="rating-table-header">
				<div class="rating-table-header-img"></div>{{ $localizeFilter('referral_program') }} \
			</div>
			<ul class="rating-table-list">
				<li class="rating-table-item" v-for="item in profitData" :key="item">
					<div class="rating-table-lvl" v-if="item.levelFrom === null"><img :src="item.img" alt="">{{
						$localizeFilter('elite').toUpperCase() }}</div>
					<div class="rating-table-lvl" v-else><img :src="item.img" alt=""> {{ item.levelFrom }} - {{ item.levelTo }}
						{{ $localizeFilter('level') }}</div>
					<div class="rating-table-referrals" v-if="item.referralsTo === null">{{ item.referralsFrom }}+ {{
						$localizeFilter('referral') }}</div>
					<div class="rating-table-referrals" v-else>{{ item.referralsFrom }} - {{ item.referralsTo }} {{
						$localizeFilter('referral') }}</div>
					<div class="rating-table-percent" v-if="item.percentTo === null">{{ item.percentFrom }}%</div>
					<div class="rating-table-percent" v-else>{{ item.percentFrom }} - {{ item.percentTo }}%</div>
				</li>
			</ul>
		</div>
		<div class="rating-table-header">
			<div class="rating-table-header-img"></div>{{ $localizeFilter('referral_program') }} \
		</div>
		<div class="wrapper">
			<div class="referral-program-table-block" :style="style" id="referral-program-table-block">
				<div class="custom-table referral-program-table">
					<div class="custom-table-header">
						<div class="custom-table-row">
							<div class="custom-table-cell cell-1">{{ $localizeFilter('level') }}</div>
							<div class="custom-table-cell cell-2">{{ $localizeFilter('referrals_from') }}</div>
							<div class="custom-table-cell cell-3">{{ $localizeFilter('referrals_to') }}</div>
							<div class="custom-table-cell cell-4">{{ $localizeFilter('level_percent') }}</div>
							<div class="custom-table-cell cell-5">{{ $localizeFilter('level_price') }}</div>
							<div class="custom-table-cell cell-6">{{ $localizeFilter('action') }}</div>
						</div>
					</div>
					<div class="custom-table-body">
						<div class="custom-table-row" v-for="(level, idx) in levels" v-bind:key="idx"
							:class="level.isCurrent ? 'current' : ''" :id="getRowId(level)">
							<div class="custom-table-cell cell-1">
								<img class="avatar" :src="level.photoUrl" v-if="level.isCurrent && level.photoUrl">
								<p class="level" :class="(level.level === 0) ? 'newbie' : ''">{{ getLevelName(level.level) }}</p>
							</div>
							<div class="custom-table-cell cell-2">
								<p>{{ level.referralsFrom }}</p>
							</div>
							<div class="custom-table-cell cell-3">
								<p>{{ level.referralsTo }}</p>
							</div>
							<div class="custom-table-cell cell-4">
								<p class="level-percent">{{ level.percent + '%' }}</p>
							</div>
							<div class="custom-table-cell cell-5">
								<p class="level-price">{{ level.price }}</p>
							</div>
							<div class="custom-table-cell cell-6 button-block">
								<button class="basic-button buy-button"
									@click="buyLevel(level)">{{ $localizeFilter('buy') }}</button>
							</div>
						</div>
					</div>
				</div>

				<!-- v-if="level.level > currentLevel.level" -->
			</div>
		</div>

		<div class="expand-block" v-if="!isExpanded">
			<div class="line"></div>
			<div class="expand-button" @click="expand()">
				<img class="arrow" src="@/assets/newImages/double-arrows-white.svg">
			</div>
			<div class="line"></div>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '../filters/localize.filter';

export default defineComponent({
	props: {
		levels: {
			type: Array,
			default: () => []
		}
	},
	data: () => ({
		style: 'height: 720px;',
		isExpanded: false,
		profitData: [
			{ levelFrom: 0, levelTo: 10, referralsFrom: 1, referralsTo: 249, percentFrom: 1, percentTo: 6.2, img: require('@/assets/newImages/profit.svg') },
			{ levelFrom: 11, levelTo: 25, referralsFrom: 250, referralsTo: 624, percentFrom: 6.4, percentTo: 9.2, img: require('@/assets/newImages/profit2.svg') },
			{ levelFrom: 26, levelTo: 51, referralsFrom: 625, referralsTo: 1249, percentFrom: 9.4, percentTo: 11.2, img: require('@/assets/newImages/profit3.svg') },
			{ levelFrom: 51, levelTo: 100, referralsFrom: 1250, referralsTo: 2499, percentFrom: 14.4, percentTo: 24.2, img: require('@/assets/newImages/profit4.svg') },
			{ levelFrom: null, levelTo: null, referralsFrom: 2500, referralsTo: null, percentFrom: 25, percentTo: null, img: require('@/assets/newImages/profit5.svg'), id: 'elite' },
		]
	}),
	setup() {

	},
	computed: {
		currentLevel() {
			return this.levels.filter(el => {
				return el.isCurrent === true
			}).length > 0 ? this.levels.filter(el => {
				return el.isCurrent === true
			})[0] : { level: -1 };
		}
	},
	methods: {
		getLevelName(level) {
			if (level === 0) {
				return localizeFilter('newbie')
			} else if (level === 101) {
				return localizeFilter('elite')
			} else {
				return localizeFilter('level') + ' ' + level
			}
		},
		getRowId(level) {
			return 'referral-program-level-' + level.level
		},
		async buyLevel(level) {
			const data = { level }
			await this.$store.dispatch('setPaymentLevelData', { data })
			this.$nextTick(() => {
				this.$store.dispatch('setShowPaymentLevelModule', { value: true, level: level.level })
			})
		},
		expand() {
			// const el = document.getElementById('referral-program-table-block')
			// if (el) {
			//     const scrollY = el.scrollTop
			//     // console.log(scrollY)
			this.$nextTick(() => {
				this.style = 'height: 5050px'
				this.isExpanded = true
				// setTimeout(()=>{
				//     window.scrollY = scrollY
				// }, 0)
			})
			// }
		}
	},
	mounted() {
		setTimeout(() => {
			const el = document.getElementById('referral-program-table-block')
			if (el) {
				const row = document.getElementById(this.getRowId(this.currentLevel))
				if (row) {
					// // console.log(el.scrollTop, Math.max((row.offsetTop-270),0))
					el.scrollTop = Math.max((row.offsetTop - 270), 0)
				}
			}
		}, 1000)
	}
})
</script>

